* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body, html {
  font-family: 'Arial', sans-serif;
  height: 100%;
}

.container {
  display: flex;
  height: 100vh;
}

.form-section {
  flex: 1;
  padding: 60px 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #fff;
}

h2 {
  margin-bottom: 20px;
  font-size: 28px;
}

form {
  display: flex;
  flex-direction: column;
}

label {
  margin-top: 15px;
  font-size: 14px;
}

input[type="text"],
input[type="email"],
input[type="password"] {
  margin-top: 5px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 6px;
}

.checkbox {
  margin: 15px 0;
  font-size: 12px;
}

button[type="submit"] {
  background-color: #3a5311;
  color: white;
  padding: 12px;
  border: none;
  border-radius: 6px;
  font-weight: bold;
  margin-top: 10px;
  cursor: pointer;
}

.or {
  text-align: center;
  margin: 20px 0;
  color: #aaa;
}

.socials {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-bottom: 20px;
}

.google,
.apple {
  padding: 10px 20px;
  border: 1px solid #ddd;
  border-radius: 6px;
  background: white;
  cursor: pointer;
}

.signin {
  text-align: center;
  font-size: 14px;
}

.signin a {
  color: #3b82f6;
  text-decoration: none;
}

.image-section {
  flex: 2;
  overflow: hidden;
}

.image-section img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
